.content{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr 3fr 3fr;
    grid-gap: 50px;
    padding: 50px;
    color: white;
    font-family: Verdana, sans-serif;
    font-size: 15px;
}
.info {
    background: lime;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 4;
    grid-row: 1;
}
.box1 {
    background: lightpink;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
}
.box2 {
    background: lightpink;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
}
.box3 {
    background: lightpink;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
