.content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 1fr 9fr;
    grid-gap: 10px;
    color: white;
    font-family: Verdana, sans-serif;
    font-size: 15px;
}
.info {
    background: brown;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 3;
    grid-row: 1;
}
.all {
    background: palevioletred;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 2;
    grid-row: 2;
}
.best {
    background: pink;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column: 2 / 3;
    grid-row: 2;
}