.content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 1fr 9fr;
    grid-gap: 10px;
    color: white;
    font-family: Verdana, sans-serif;
    font-size: 15px;
}
.up {
    background: orange;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 3;
    grid-row: 1;
}
.down {
    background: lightsalmon;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column: 1 / 3;
    grid-row: 2;
}