.content{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr 9fr;
    grid-gap: 10px;
    color: white;
    font-family: Verdana, sans-serif;
    font-size: 15px;
}
.head{
    background: plum;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 4;
    grid-row: 1;
}
.fields{
    background: darkslategray;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    min-height: 50px;
    grid-column: 1 / 2;
    grid-row: 2;
}
.massager {
    background: darkolivegreen;
    padding: 10px;
    border: solid 2px darkgrey;
    border-radius: 8px;
    grid-column: 2 / 4;
    grid-row: 2;
}